nav {
    position: fixed;
    top: 0;
    background-color: #fff;
    width: 100%;
}

nav > ul {
    display: flex;
    list-style: none;
    gap: 40px;
    align-items: center;
}

nav > ul > li > a {
    text-decoration: none;
    color: rgb(66, 66, 66);
    font-weight: 500;
    font-size: 18px;
}

.float-right > a {
    font-size: 26px;
    color: Green;

}